<template>
  <div class="examine">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/admin/home">首页</router-link></a-breadcrumb-item>
        <a-breadcrumb-item><router-link to="/admin/person/reader">读者管理</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>读者审核</a-breadcrumb-item>
      </a-breadcrumb>
    </div>

    <div class="margin_bottom_20">
      <a-checkbox-group @change="handleStatusChange">
        <span>审核状态：</span>
        <a-checkbox :value="3">
          待审核
        </a-checkbox>
        <a-checkbox :value="2">
          未通过
        </a-checkbox>
      </a-checkbox-group>
    </div>

    <div class="btn_group margin_bottom_20">
      <a-button type="primary" @click="toReader">读者列表</a-button>
        <a-button type="primary">批量审核</a-button>
    </div>
    
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="gender" slot-scope="text">
        {{ text.gender === 1 ? '男' : '女'}}
      </template>
      <template slot="status" slot-scope="text">
        {{ formatStatus(text.status) }}
      </template>
      <template slot="id_pos" slot-scope="text">
        <div
          class="id_img"
          :style="`background-image: url(${proxy + '/cbh_file/' +text.id_card_positive_img});`"
        />
      </template>
      <template slot="id_nev" slot-scope="text">
        <div
          class="id_img"
          :style="`background-image: url(${proxy + '/cbh_file/' +text.id_card_positive_img});`"
        />
      </template>
      <template slot="action" slot-scope="text">
        <a-button v-if="text.status !== 0" type="link" @click="openExamine(text.user_id)">审核</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="examineVisible"
      title="读者审核"
      @ok="submitExamine"
    >
      <a-radio-group v-model="examineParams.status" @change="handleExamine">
        <a-form>
          <a-form-item label="审核状态">
            <a-radio :value="1">
              审核通过
            </a-radio>
            <a-radio :value="2">
              审核不通过
            </a-radio>
          </a-form-item>
          <a-form-item label="备注">
            <a-input type="textarea" v-model="examineParams.remark" :rows="4" />
          </a-form-item>
        </a-form>
        
      </a-radio-group>
    </a-modal>

  </div>
</template>
<script>
import { Checkbox } from 'ant-design-vue'
import STable from '@/components/Table'
import { mapActions, mapState } from 'vuex'
import Qs from 'qs'
export default {
  name: 'ReaderExamine',
  components: { ACheckbox: Checkbox, ACheckboxGroup: Checkbox.Group, STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '姓名',
        dataIndex: 'name'
      },
      {
        title: '性别',
        scopedSlots: { customRender: "gender"},
      },
      {
        title: '手机',
        dataIndex: 'phone'
      },
      {
        title: '身份证号',
        dataIndex: 'id_card'
      },
      {
        title: '提交申请时间',
        dataIndex: 'create_time'
      },
      {
        title: '身份证正面',
        scopedSlots: { customRender: "id_pos"},
      },
      {
        title: '身份证反面',
        scopedSlots: { customRender: "id_nev"},
      },
      {
        title: '审核状态',
        scopedSlots: { customRender: "status"},
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]

    let examineParams = {
      user_id: [],
      status: 1,
      remark: ''
    }

    return {
      columns,
      examineParams,
      examineVisible: false,
      status: [1,2,3],
      currentExamineId: null,
      proxy: process.env.VUE_APP_API_URL,
      loadData: paramters => {
        return this.getneedExamineReaderList({
          params: Object.assign({}, paramters),
          status: Qs.stringify({status: this.status}, { arrayFormat: 'repeat' })
        })
      }
    }
  },
  computed: {
    formatStatus() {
      return code => {
        return code === 1 ? '已审核' : code === 2 ? '未通过' : '待审核'
      }
    },
  },
  methods: {
    ...mapActions('reader', {
      getneedExamineReaderList: 'getNeedExamineReaderList',
      readerVertify: 'readerVertify'
    }),
    handleStatusChange(val) {
      if (val.length === 0) {
        this.status = [1,2,3]
      } else {
        this.status = val
      }
      this.$refs.table.refresh(true)
    },
    toReader() {
      this.$router.push({
        path: '/admin/person/reader'
      })
    },
    handleExamine(e) {
      this.examineParams.status = e.target.value
    },
    openExamine(id) {
      this.examineVisible = true
      this.examineParams.user_id.push(id)
    },
    submitExamine(){
      this.readerVertify(this.examineParams).then(res => {
        this.$message.success('操作成功')
        this.examineVisible = false
        this.$refs.table.refresh(true)
      })
    }
  }
}
</script>
<style>
.btn_group {
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
}
.id_img {
  width: 88px; 
  height: 88px;
  background-position: center;
  background-size: cover;
}
</style>